'use client'

import { useRef, useEffect, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Autoplay } from 'swiper';
import 'swiper/swiper-bundle.css';
import {
  fetchFeaturedMovies,
  fetchGenres,
  fetchMoviesByGenre,
  getLogos
} from '@/lib/api/tmdb';
import Link from 'next/link';

// Fuente personalizada
import { Bebas_Neue } from 'next/font/google'

const bebas = Bebas_Neue({
  weight: '400',
  subsets: ['latin'],
})

export default function MainDashboard() {
  const [ready, setReady] = useState(false);
  const [dashboardData, setDashboardData] = useState({
    featuredMovies: [],
    genreFilters: [],
    filterMovies: {}
  });

  const prevRef = useRef(null)
  const nextRef = useRef(null)

  useEffect(() => {
    const loadData = async () => {
      try {
        // Cargar películas destacadas y géneros en paralelo
        const [movies, genres] = await Promise.all([
          fetchFeaturedMovies(),
          fetchGenres()
        ]);

        // Cargar logos en paralelo
        const moviesWithLogos = await Promise.all(
          movies.map(async (movie) => {
            const logoPath = await getLogos('movie', movie.id);
            return { ...movie, logo_path: logoPath };
          })
        );

        // Cargar películas por género en paralelo
        const genreMoviePairs = await Promise.all(
          genres.map(async (genre) => {
            const movies = await fetchMoviesByGenre(genre.id);
            return [genre.name, movies];
          })
        );

        const genreMoviesObj = Object.fromEntries(genreMoviePairs);

        // Almacenar todo junto
        setDashboardData({
          featuredMovies: moviesWithLogos,
          genreFilters: genres,
          filterMovies: genreMoviesObj
        });

        setReady(true);
      } catch (err) {
        console.error("Error cargando dashboard:", err);
      }
    };

    loadData();
  }, []);

  if (!ready) return null;

  const { featuredMovies, genreFilters, filterMovies } = dashboardData;

  return (
    <div className="p-8 text-white bg-black">
      {/* Carrusel de películas destacadas */}
      <div className="relative group">
        <Swiper
          spaceBetween={20}
          slidesPerView={3}
          autoplay={{ delay: 5000 }}

          navigation={{
            prevEl: prevRef.current,
            nextEl: nextRef.current,
          }}
          onInit={(swiper) => {
            // Reparar refs dinámicos
            swiper.params.navigation.prevEl = prevRef.current
            swiper.params.navigation.nextEl = nextRef.current
            swiper.navigation.init()
            swiper.navigation.update()
          }}
          
          modules={[Navigation, Autoplay]}
          className="h-full group relative"
        >
          {featuredMovies.map((movie) => (
            <SwiperSlide key={movie.id}>
              <Link href={`/details/movie/${movie.id}`}>
                <div className="relative cursor-pointer">
                  <img
                    src={`https://image.tmdb.org/t/p/original${movie.backdrop_path}`}
                    alt={movie.title}
                    className="w-full h-full object-cover rounded-lg"
                  />
                  {movie.logo_path && (
                    <img
                      src={`https://image.tmdb.org/t/p/w200${movie.logo_path}`}
                      alt={`${movie.title} logo`}
                      className="absolute bottom-2 right-4 h-18 max-w-[340px] object-contain"
                    />
                  )}
                </div>
              </Link>
            </SwiperSlide>
          ))}
  
          {/* Flechas personalizadas */}
          <div className="swiper-button-prev !text-white !w-8 !h-8 !flex !items-center !justify-center opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
          <div className="swiper-button-next !text-white !w-8 !h-8 !flex !items-center !justify-center opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
        </Swiper>
      </div>
  
      {/* Carruseles por género */}
      <div className="mt-12 space-y-12">
        {genreFilters.map((genre) => (
          <div key={genre.id} className="relative group">
            <h3
              className={`text-3xl tracking-wide text-white mb-4 ${bebas.className} uppercase`}
            >
              {genre.name}
            </h3>

            <Swiper
              spaceBetween={20}
              slidesPerView={10}
              navigation={{
                prevEl: prevRef.current,
                nextEl: nextRef.current,
              }}
              onInit={(swiper) => {
                // Reparar refs dinámicos
                swiper.params.navigation.prevEl = prevRef.current
                swiper.params.navigation.nextEl = nextRef.current
                swiper.navigation.init()
                swiper.navigation.update()
              }}
              modules={[Navigation]}
              className="mt-6 group relative"
              breakpoints={{
                640: { slidesPerView: 2, spaceBetween: 10 },
                768: { slidesPerView: 4, spaceBetween: 10 },
                1024: { slidesPerView: 10, spaceBetween: 20 },
              }}
            >
              {(filterMovies[genre.name] || []).map((movie) => (
                <SwiperSlide key={movie.id}>
                  <Link href={`/details/movie/${movie.id}`}>
                    <div className="cursor-pointer">
                      <img
                        src={`https://image.tmdb.org/t/p/w300${movie.poster_path}`}
                        alt={movie.title}
                        className="w-full h-full object-cover rounded-lg"
                      />
                    </div>
                  </Link>
                </SwiperSlide>
              ))}
  
              {/* Flechas personalizadas */}
              <div className="swiper-button-prev !text-white !w-8 !h-8 !flex !items-center !justify-center opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
              <div className="swiper-button-next !text-white !w-8 !h-8 !flex !items-center !justify-center opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
            </Swiper>
          </div>
        ))}
      </div>
    </div>
  );  
}
